// Main ToolsPage component
export default function ToolsPage() {
  const [tools, setTools] = useState<Tool[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [showAddModal, setShowAddModal] = useState(false);
  const [editingTool, setEditingTool] = useState<Tool | null>(null);
  const router = useRouter();

  useEffect(() => {
    const fetchTools = async () => {
      try {
        const response = await axios.get("http://192.168.110.100:8080/data2");
        let toolsData = response.data;

        if (
          toolsData &&
          typeof toolsData === "object" &&
          !Array.isArray(toolsData)
        ) {
          if (Array.isArray(toolsData.data)) {
            toolsData = toolsData.data;
          } else if (Array.isArray(toolsData.tools)) {
            toolsData = toolsData.tools;
          }
        }

        if (!Array.isArray(toolsData)) {
          throw new Error("API did not return an array of tools");
        }

        const processedTools = toolsData.map((t: any, index: number) => ({
          id: t.id?.toString() || `generated-${index}-${Date.now()}`,
          item_name: t.item_name || "No name",
          stock: t.stock || 0,
          item_condition: t.item_condition || "Baik",
          category: t.category || "Lainnya",
        }));

        setTools(processedTools);
      } catch (err) {
        console.error("Fetch error:", err);
        setError(err instanceof Error ? err.message : "Unknown error occurred");
        toast.error("Gagal memuat data alat");
      } finally {
        setLoading(false);
      }
    };

    fetchTools();
  }, []);

  const API_BASE_URL = "http://192.168.110.100:8080/data2";

  const handleSaveNewTool = async (newTool: ToolFormData) => {
    try {
      let response;
      try {
        response = await axios.post(`${API_BASE_URL}/add`, newTool);
      } catch (addError) {
        if (axios.isAxiosError(addError) && addError.response?.status === 404) {
          response = await axios.post(API_BASE_URL, newTool);
        } else {
          throw addError;
        }
      }
      const createdTool = {
        ...newTool,
        id: response.data.id || `generated-${Date.now()}`,
      };
      setTools([...tools, createdTool]);
      setShowAddModal(false);
      toast.success("Alat berhasil ditambahkan");
    } catch (err) {
      console.error("Error adding tool:", err);
      toast.error(
        `Gagal menambahkan alat: ${
          axios.isAxiosError(err)
            ? err.response?.data?.message || err.message
            : "Unknown error"
        }`
      );
    }
  };

  const handleSaveEditedTool = async (editedTool: Tool) => {
    try {
      // Pastikan stock dikirim sebagai number
      const payload = {
        ...editedTool,
        stock: Number(editedTool.stock),
      };

      // Coba endpoint edit utama
      try {
        await axios.put(`${API_BASE_URL}/edit/${editedTool.id}`, payload);
      } catch (editError) {
        // Jika endpoint /edit/404, fallback ke endpoint lama
        if (axios.isAxiosError(editError) && editError.response?.status === 404) {
          await axios.put(`${API_BASE_URL}/${editedTool.id}`, payload);
        } else {
          throw editError;
        }
      }

      // Ambil data terbaru dari API agar state sinkron dengan database
      const refresh = await axios.get(API_BASE_URL);
      let toolsData = refresh.data;
      if (toolsData && typeof toolsData === "object" && !Array.isArray(toolsData)) {
        if (Array.isArray(toolsData.data)) {
          toolsData = toolsData.data;
        } else if (Array.isArray(toolsData.tools)) {
          toolsData = toolsData.tools;
        }
      }
      const processedTools = toolsData.map((t: any, index: number) => ({
        id: t.id?.toString() || `generated-${index}-${Date.now()}`,
        item_name: t.item_name || "No name",
        stock: t.stock || 0,
        item_condition: t.item_condition || "Baik",
        category: t.category || "Lainnya",
      }));
      setTools(processedTools);

      setEditingTool(null);
      toast.success("Perubahan berhasil disimpan");
    } catch (err) {
      console.error("Error updating tool:", err);
      toast.error(
        `Gagal menyimpan perubahan: ${
          axios.isAxiosError(err)
            ? err.response?.data?.message || err.message
            : "Unknown error"
        }`
      );
    }
  };

  const handleDeleteTool = async (toolId: string) => {
    try {
      await axios.delete(`${API_BASE_URL}/delete/${toolId}`);
      setTools(tools.filter((tool) => tool.id !== toolId));
      toast.success("Alat berhasil dihapus");
    } catch (err) {
      console.error("Error deleting tool:", err);
      toast.error(
        `Gagal menghapus alat: ${
          axios.isAxiosError(err)
            ? err.response?.data?.message || err.message
            : "Unknown error"
        }`
      );
    }
  };

  const handleEditClick = (tool: Tool) => {
    setEditingTool(tool);
  };

  const handleAddModalOpen = () => {
    setShowAddModal(true);
  };

  const handleAddModalClose = () => {
    setShowAddModal(false);
  };

  const handleEditModalClose = () => {
    setEditingTool(null);
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-screen">
        <svg
          className="animate-spin h-5 w-5 text-blue-600"
          xmlns="http://www.w3.org/2000/svg"
          fill="none"
          viewBox="0 0 24 24"
        >
          <circle
            className="opacity-25"
            cx="12"
            cy="12"
            r="10"
            stroke="currentColor"
            strokeWidth="4"
          ></circle>
          <path
            className="opacity-75"
            fill="currentColor"
            d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
          ></path>
        </svg>
      </div>
    );
  }

  if (error) {
    return (
      <div className="flex items-center justify-center h-screen">
        <div className="text-center">
          <h2 className="text-xl font-semibold text-gray-800 mb-2">
            Terjadi Kesalahan
          </h2>
          <p className="text-gray-600 text-sm mb-4">{error}</p>
          <button
            onClick={() => router.reload()}
            className="px-4 py-2 bg-gradient-to-r from-blue-500 to-indigo-600 text-white rounded-lg shadow-md hover:from-blue-600 hover:to-indigo-700 transition"
          >
            Coba Lagi
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="flex flex-col lg:flex-row">
      <Sidebar />
      <div className="flex-1 p-5">
        <div className="flex justify-between items-center mb-5">
          <h1 className="text-2xl font-bold text-gray-800">Daftar Alat</h1>
          <button
            onClick={handleAddModalOpen}
            className="px-4 py-2 bg-gradient-to-r from-blue-500 to-indigo-600 text-white rounded-lg shadow-md hover:from-blue-600 hover:to-indigo-700 transition"
          >
            Tambah Alat Baru
          </button>
        </div>

        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4">
          {tools.map((tool) => (
            <ToolCard
              key={tool.id}
              tool={tool}
              onEdit={handleEditClick}
              onDelete={handleDeleteTool}
            />
          ))}
        </div>
      </div>

      {/* Edit Tool Modal */}
      {editingTool && (
        <EditToolModal
          tool={editingTool}
          onSave={handleSaveEditedTool}
          onCancel={handleEditModalClose}
        />
      )}

      {/* Add Tool Modal */}
      {showAddModal && (
        <AddToolModal onSave={handleSaveNewTool} onCancel={handleAddModalClose} />
      )}
    </div>
  );
}
